/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}
html ::-webkit-scrollbar {
  display: none;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background-color: #2C2E43;
}
body.body-scroll {
  padding-top: 28.8px;
}

p {
  font-size: 2rem;
  color: #B2B1B9;
  margin: 0;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

img, picture {
  width: 100%;
  display: block;
}

h1 {
  font-size: 3.8rem;
  color: #fff;
  margin: 0;
  padding: 0;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-weight: 600;
  color: #B2B1B9;
  font-size: 3rem;
  margin: 0;
}

h4 {
  color: #B2B1B9;
  font-size: 2.2rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

header {
  position: fixed;
  z-index: 9000;
  top: 0;
  left: 0;
  width: 100%;
}

/*Menu hamburguesa*/
.menu-hamb {
  display: none;
}

/*NAV*/
.estilo {
  display: flex;
  justify-content: center;
  gap: 2rem;
  border-bottom: 1px solid #FFD523;
  margin-right: 20%;
  margin-left: 20%;
  transition: all 0.5 ease;
}
.estilo .inicio {
  display: none;
}
.estilo a {
  color: #B2B1B9;
  font-size: 2rem;
}
.estilo a:hover {
  filter: brightness(1.9);
}

nav.navegacion-header.abajo {
  background: #393C58;
  box-shadow: 0.1rem 0.2rem 0.3rem #222;
  padding: 1rem;
  border-bottom: 0px;
  width: 100%;
  margin: 0;
  position: absolute;
  top: 0;
  z-index: 9000;
}
nav.navegacion-header.abajo a:hover {
  filter: brightness(1.9);
}
nav.navegacion-header.abajo .inicio {
  display: block;
}

/*navegador 2*/
.nav2 {
  display: none;
}

.abc {
  display: flex;
  justify-content: flex-end;
  margin: 1rem;
}

.menu-hamb {
  display: none;
  padding: 0.5rem;
}
.menu-hamb img {
  width: 4.5rem;
}

.cerrar {
  display: none;
}
.cerrar img {
  width: 6rem;
}

@media (max-width: 860px) {
  .nav-h {
    display: none;
  }
  .estilo {
    border-bottom: 0;
  }
  .nav2 {
    display: block;
    background: #2C2E43;
    width: 5rem;
    position: absolute;
    top: 0;
    right: 0;
  }
  .menu-hamb,
  .cerrar {
    display: block;
    cursor: pointer;
  }
  .navsi {
    opacity: 0;
    visibility: hidden;
  }
  .navsi {
    background: #2C2E43;
    position: absolute;
    top: 0;
    right: 0;
    background-color: #393C58;
    gap: 0.5rem;
    box-shadow: 0 0 0 100vmax rgba(0, 0, 0, 0.5);
  }
  .navsi .nav-lista {
    flex-direction: column;
    border-bottom: 0px;
    align-items: end;
  }
  .navsi .nav-lista .inicio {
    display: block;
  }
  .visible {
    opacity: 1;
    visibility: visible;
    padding: 0rem 0rem 1rem 4rem;
    border-radius: 0 0 0rem 7rem;
  }
  .slideIn {
    opacity: 0;
    transform: translateY(-100%);
    transition: opacity 0.6s ease, transform 0.6s ease;
  }
  .slideIn.visible {
    opacity: 1;
    transform: translateY(0%);
  }
}
.contenedor_nombre {
  margin-bottom: 15rem;
}

.contenido-header {
  display: flex;
  align-content: center;
  flex-direction: column;
  flex-wrap: wrap;
  margin-top: 13rem;
  text-align: center;
}
.contenido-header h1 {
  font-size: 6rem;
  margin-top: -5rem;
}
.contenido-header ul {
  display: flex;
  gap: 3.5rem;
  margin-top: 2rem;
}
.contenido-header ul img {
  width: 4rem;
  height: 4rem;
  margin-top: 1rem;
}
.contenido-header li {
  margin: 1rem;
  width: 5rem;
  height: 5rem;
}
.contenido-header li p {
  display: none;
}
.contenido-header li:hover p {
  filter: brightness(1.9);
  display: block;
  width: 4rem;
  text-align: center;
  font-size: 1.3rem;
}
.contenido-header img:hover {
  width: 4.1rem;
  filter: brightness(1.9);
}
.contenido-header .autotype {
  color: #B2B1B9;
}
.contenido-header .typed-cursor {
  color: #FFD523;
}

/*BOTON*/
.nombre_botones {
  display: flex;
  height: 10rem;
  gap: 2rem;
  padding-top: 2rem;
}

.boton {
  background-color: #FFD523;
  min-width: 16rem;
  color: #2C2E43;
  text-transform: uppercase;
  font-family: sans-serif;
  border: none;
  font-size: 1.3rem;
  padding: 1.8rem;
  margin-top: 3rem;
  border-radius: 1rem;
}
.boton:hover {
  font-size: 1.51rem;
  cursor: pointer;
  filter: brightness(1.1);
}

@media (max-width: 830px) {
  .contenido-header {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-content: center;
    gap: 0;
  }
  .contenido-header h1, .contenido-header h3, .contenido-header p {
    text-align: center;
  }
  .boton {
    background-color: #FFD523;
    color: #2C2E43;
    text-transform: uppercase;
    border: none;
    font-size: 1.5rem;
    padding: 1.8rem;
    margin: 1rem auto;
    border-radius: 1rem;
  }
}
footer {
  background-color: #393C58;
  margin-top: 4rem;
}
footer a {
  color: #B2B1B9;
  font-size: 1.5rem;
  gap: 0.5rem;
}
footer p {
  font-size: 1.5rem;
  text-align: center;
}
footer ul {
  display: flex;
  justify-content: center;
  gap: 3.5rem;
  margin: 0;
}
footer ul img {
  width: 4rem;
  height: 4rem;
  margin-top: 1rem;
}
footer li {
  margin: 1rem;
  width: 5rem;
  height: 8rem;
}
footer li p {
  font-size: 1.2rem;
  width: 4rem;
  text-align: center;
}
footer li:hover li {
  font-size: 1.3rem;
  filter: brightness(1.9);
}
footer img:hover {
  width: 4.01rem;
  height: 4.1rem;
  filter: brightness(1.9);
}

.sobremi {
  display: flex;
  justify-content: center;
  gap: 6rem;
}

.sobremi_texto {
  width: 50%;
}

.img-sobremi img {
  width: 30rem;
}

@media (max-width: 860px) {
  .sobremi {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-content: center;
    text-align: center;
  }
  .sobremi .sobremi_texto {
    width: 80%;
  }
  .sobremi .img-sobremi img {
    width: 30rem;
    margin: 0 auto;
  }
}
.habilidades {
  padding: 8rem 0;
}
.habilidades h3 {
  padding-bottom: 1rem;
  text-align: center;
}
.habilidades img {
  width: 10rem;
  height: 10rem;
}
.habilidades ul {
  display: flex;
  justify-content: center;
  gap: 2rem;
}
.habilidades li {
  margin: 1rem;
  width: 11rem;
  height: 14rem;
}
.habilidades li p {
  display: block;
  width: 10rem;
  text-align: center;
  font-size: 1.3rem;
}
.habilidades li:hover p {
  font-size: 1.5rem;
  filter: brightness(1.9);
}
.habilidades li:hover img {
  filter: brightness(1.1);
  width: 11rem;
  height: 11rem;
}

@media (max-width: 1060px) {
  .habilidades ul {
    margin: 0 20%;
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 397px) {
  .habilidades ul {
    margin: 0;
  }
}
.proyectos-contenedor {
  min-height: 100vh;
}
.proyectos-contenedor h3 {
  font-weight: 400;
  text-align: center;
  margin: 0;
  padding: 0;
}

.proyectos-demo {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 2rem;
}
.proyectos-demo h3:hover {
  filter: brightness(1.5);
}
.proyectos-demo p {
  margin: 0;
  font-size: 1.6rem;
  padding: 0 1rem;
}
.proyectos-demo img {
  width: 55rem;
  height: 30rem;
  box-shadow: 0.1rem 0.2rem 0.5rem #222;
}
.proyectos-demo img:hover {
  transform: translateY(-5px);
  box-shadow: 0.8rem 1rem 0.5rem #2d2b2b;
}

img.construccion {
  width: 25rem;
  box-shadow: none;
}
img.construccion:hover {
  transform: none;
  box-shadow: none;
}

img.mini {
  width: 4rem;
  height: 5rem;
  box-shadow: none;
}
img.mini:hover {
  transform: none;
  box-shadow: none;
}

@media (min-width: 1080px) {
  .contenedor-proyectos {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 1024px) {
  .proyectos-demo img {
    width: 45rem;
  }
  .proyectos-demo img.construccion {
    width: 18rem;
  }
}
@media (max-width: 425px) {
  .proyectos-demo img {
    width: 38rem;
  }
}
.contacto a {
  color: lightblue;
  font-size: 1.5rem;
  gap: 0.5rem;
}
.contacto img {
  width: 4rem;
  height: 4rem;
}
.contacto p {
  color: lightblue;
  font-size: 1.5rem;
}
.contacto li {
  display: flex;
  flex-wrap: wrap;
  align-content: center;
  gap: 0.5rem;
  margin-top: 1rem;
  text-transform: uppercase;
}